ius-rounded\n      +ltr-property(\"padding\", 1em, false)\n  select\n    @extend %input\n    cursor: pointer\n    display: block\n    font-size: 1em\n    max-width: 100%\n    outline: none\n    &::-ms-expand\n      display: none\n    &[disabled]:hover,\n    fieldset[disabled] &:hover\n      border-color: $input-disabled-border-color\n    &:not([multiple])\n      +ltr-property(\"padding\", 2.5em)\n    &[multiple]\n      height: auto\n      padding: 0\n      option\n        padding: 0.5em 1em\n  // States\n  &:not(.is-multiple):not(.is-loading):hover\n    &::after\n      border-color: $input-hover-color\n  // Colors\n  @each $name, $pair in $colors\n    $color: nth($pair, 1)\n    &.is-#{$name}\n      &:not(:hover)::after\n        border-color: $color\n      select\n        border-color: $color\n        &:hover,\n        &.is-hovered\n          border-color: bulmaDarken($color, 5%)\n        &:focus,\n        &.is-focused,\n        &:active,\n        &.is-active\n          box-shadow: $input-focus-box-shadow-size bulmaRgba($color, 0.25)\n  // Sizes\n  &.is-small\n    +control-small\n  &.is-medium\n    +control-medium\n  &.is-large\n    +control-large\n  // Modifiers\n  &.is-disabled\n    &::after\n      border-color: $input-disabled-color\n  &.is-fullwidth\n    width: 100%\n    select\n      width: 100%\n  &.is-loading\n    &::after\n      @extend %loader\n      margin-top: 0\n      position: absolute\n      +ltr-position(0.625em)\n      top: 0.625em\n      transform: none\n    &.is-small:after\n      font-size: $size-small\n    &.is-medium:after\n      font-size: $size-medium\n    &.is-large:after\n      font-size: $size-large\n","$file-border-color: $border !default\n$file-radius: $radius !default\n\n$file-cta-background-color: $scheme-main-ter !default\n$file-cta-color: $text !default\n$file-cta-hover-color: $text-strong !default\n$file-cta-active-color: $text-strong !default\n\n$file-name-border-color: $border !default\n$file-name-border-style: solid !default\n$file